@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}

/*HOME*/

.home {
    background: rgb(211, 240, 255);
    width: 100vw;
    min-height: 100vh;
    padding: 50px 120px;
}

.home .home-title h1 {
    width: 300px;
    font-size: 52px;
    font-weight: 300;
    text-align: center;
}

.home .home-info {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-top: 56px;
}

.home .home-info-img {
    width: 600px;
    text-align: center;
}

.home .home-info-img img {
    width: 400px;
    height: 400px;
}

.home .home-info-text {
    width: 600px;
}

.home .home-info-text-border {
    background: #fff;
    text-align: center;
    padding: 20px;
    border: solid 6px rgb(157, 251, 240);
    border-radius: 15px;
    box-shadow: -10px 5px 5px #797979;
}

.home .home-info-btn {
    margin-top: 40px;
    text-align: center;
}

.bnt-connect {
    display: none;
    background: rgb(255, 144, 183);
    width: 248px;
    height: 64px;
    font-weight: 200;
    font-size: 30px;
    color: white;
    box-shadow: inset -1px 2px 4px #000000;
    border-radius: 15px;
    transition: all .5s ease-in-out;
}

.bnt-connect:hover {
    cursor: pointer;
    color: #000;
    transition: all .5s ease-in-out;
}

/*Responsive*/

@media (max-width: 1200px) {
    .home .home-info-img img {
        width: 350px;
        height: 350px;
    }
}

@media (max-width: 1100px) {
    .home {
        height: 100%;
    }

    .home .home-title h1 {
        width: 100%;
        font-size: 50px;
    }

    .home .home-info {
        flex-wrap: wrap;
        justify-content: center;
    }

    .home {
        padding: 40px 10px 10px 10px;
    }

    .home .home-info-text {
        width: 100%;
    }

    .home .home-info-text-border {
        width: 100%;
    }
}

@media (max-width: 375px) {
    .home .home-title h1 {

        font-size: 40px;
    }

    .home {
        padding-top: 80px;
        padding-bottom: 10px;
    }
}
.navbar {
    background: red;
    width: 100%;
}

.navbar .nav-list {
    position: absolute;
    right: 0;
    width: 30%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 50px;
}

.navbar .nav-list li{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.navbar .nav-list li a{
    text-decoration: none;
    color: #000;
    font-size: 20px;
    transition: all .2s ease-in-out;
}

.navbar .nav-list li a:hover{
    color: #ffffff;
    transition: all .2s ease-in-out;
}

@media (max-width: 1000px) {
    .navbar .nav-list {
        width: 80%;
    }
}

@media (max-width: 375px) {
    .hide {
        display: none;
    }
}
.thematic {
    margin-bottom: 20px;
    overflow-x: hidden;
}

.thematic .coming-soon {
    background: rgb(169,212,234);
    width: 100%;
    height: 36px;
    display: flex;
    text-align: center;
    justify-content: center;
    font-size: 24px;
    color: white;
}

.thematic .buy-opensea {
    width: 100%;
    display: flex;
    text-align: center;
    justify-content: center;
}

.thematic .buy-opensea a {
    background: rgb(105,244,200);
    text-decoration: none;
    font-size: 24px;
    color: #000;
    margin-top: 4px;
    width: 250px;
    height: 40px;
    border-radius: 8px;
    box-shadow: -10px 5px 5px #797979;
}

.thematic-rarity {
    background: rgb(241,243,244);
    width: 100%;
    min-height: 100vh;
    overflow-x: hidden;
}

.t-r {
    width: 650px;
    position: relative;
    text-align: center;
    left: 160px;
}

.t-r-title {
    padding-top: 24px;
    margin-bottom: 16px;
}

.grid {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    padding: 24px;
    grid-gap: 1em;
    gap: 1em;
}

.grid-paises {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin-top: 20px;
}

.grid-paises .paises {
    display: flex;
    flex-direction: row;
}

.grid-paises .paises img{
    width: 140px;
    height: 140px;
    margin-right: 16px;
    margin-top: 50px;
}

.grid-paises .legendarios {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.legendarios-text {
    display: flex;
    flex-direction: column;
}

.legendarios-container img {
    width: 140px;
    height: 140px;
    margin-top: 24px;
    margin-left: -20px;
}

.grid-categorias {
    display: flex;
    flex-direction: column;
}

.g-img {
    text-align: center;
}

.g-img img{
    position: relative;
    z-index: 1;
    width: 450px;
    height: 450px;
    margin-top: -80px;
    margin-left: -80px;
}

.paises-text {
    background: #ffffff;
    width: 500px;
    padding: 16px;
    border-radius: 8px;
    box-shadow: -10px 5px 5px #797979;
}

.p-text-title {
    font-size: 20px;
    font-weight: bold;
}

.legendarios {
    margin-top: 24px;
}

.legen-img {
    width: 380px;
    height: 380px;
}

.legendarios-text {
    position: relative;
    background: rgb(255,144,183);
    padding: 16px;
    border-radius: 8px;
    box-shadow: -10px 5px 5px #797979;
    color: #ffffff;
    z-index: 2;
}

.g-categorias {
    margin-top: -40px;
    padding: 60px;
}

.rarezas {
    margin-top: 40px;
    margin-bottom: 60px;
    display: none;
}


@media (max-width: 1275px) {
    .grid {
        grid-template-columns: 1fr;
    }

    .g-img img{
        width: 600px;
        height: 600px;
        margin-top: 0;
        margin-left: 0;
    }

    .g-c-text {
        text-align: center  ;
    }

    .g-c-text span {
        margin-bottom: 10px;
    }

    .t-r {
        position: static;
        width: 100%;
        text-align: center;
        padding: 20px;

    }

    .rarezas {
        display: block;
    }

}

@media (max-width: 700px) {
    .grid-paises .paises {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .grid-paises .paises img{
        width: 300px;
        height: 300px;
        margin-top: 25px;
        margin-bottom: 50px;
    }

    .legen-img {
        width: 300px;
        height: 300px;
        margin-left: 0;
    }

    .legendarios-container img {
        display: none;
    }

    .grid-paises .legendarios {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .legendarios-container {
        margin-left: 0;
    }

    .g-img img{
        width: 500px;
        height: 500px;
    }
}

@media (max-width: 575px) {
    .g-img img{
        width: 350px;
        margin-top: 0px;
        margin-left: 0px;
        height: 350px;
    }

    .g-categorias {
        margin-top: 0px;
    }

    .grid-categorias {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .legendarios-container {
        width: 300px;
    }

    .paises-text {
        width: 300px;
    }
}

@media (max-width: 400px) {
    .legendarios-container {
        width: 240px;
    }

    .paises-text {
        width: 240px;
    }

    .g-img img{
        width: 240px;
        height: 240px;
    }

    .grid-paises .paises img{
        width: 250px;
        height: 250px;
    }

    .legen-img {
        width: 250px;
        height: 250px;
    }
}
.why-get {
    padding: 24px;
}

.why-get .wg-1 {
    display: grid;
    grid-template-columns: 45% 55%;
}

.why-get .wg-1 img {
    width: 100%;
    margin-top: 0px;
}

.wg-1-text {
    text-align: right;
}

.wg-1-text .w-title {
    font-size: 24px;
}

.wg-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: center;
    margin-top: -200px;
}

.wg-2-title {
    margin-top: -54px;
}

.wg-2-title p{
    font-size: 36px;
}

.wg-2-title img {
    width: 420px;
    height: 420px;
    margin-top: -80px;
}

.wg-2-img2 img {
    width: 420px;
    height: 420px;
    padding-top: 60px;
}

@media (max-width: 1175px) {
    .wg-2 {
        margin-top: -80px;
    }
}

@media (max-width: 900px) {

    .why-get .wg-1 {
        display: grid;
        grid-template-columns: 1fr;
    }

    .w-title {
        margin-bottom: 20px;
    }

    .wg-1-text {
        text-align: center;
        margin-top: -300px;
    }

    .wg-2 {
        grid-template-columns: 1fr;
        margin-top: 0;
    }

    .wg-2-title {
        margin-top: 40px;
    }
}

@media (max-width: 700px) {
    .wg-1-text {
        margin-top: -200px;
    }
}

@media (max-width: 530px) {
    .wg-2-img2 img {
        width: 320px;
    }
    .wg-1-text {
        margin-top: -100px;
    }

    .wg-2-title img {
        width: 320px;
        height: 320px;
        margin-top: 0;
    }

    .wg-2-img2 img {
        width: 320px;
        height: 320px;
        padding-top: 0;
    }
}

@media (max-width: 375px) {
    .wg-2-img2 img {
        width: 240px;
    }

    .wg-2-title img {
        width: 240px;
    }
}
.future {
    background: rgb(169,212,234);
    padding: 24px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    grid-gap: 2em;
    gap: 2em;
}

.our-future {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
}

.our-future p {
    font-size: 28px;
    margin-bottom: 16px;
}

.our-future img {
    width: 300px;
}

.who-we-are {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.wwa {
    margin: 8px 0;
}

.wwa-who {
    font-size: 28px;
}

@media (max-width: 950px) {
    .future {
        grid-template-columns: 1fr;
    }
}
.faq {
    padding: 24px;
    background: #000;
    color: white;
    display: grid;
    grid-gap: 1em;
    gap: 1em;
    grid-template-columns: repeat(2, 1fr);
}

.faq-1 {
    display: flex;
    flex-direction: column;
}

.faq-1 h1 {
    font-size: 48px;
}

.faq-1 p {
    margin: 24px 0;
    font-size: 20px;
}

.faq-1 a {
    text-decoration: none;
}

.faq-2 {
    display: flex;
    flex-direction: column;
}

.faq-2 p {
    margin: 24px 0;
    font-size: 20px;
}

@media (max-width: 850px) {
    .faq {
        grid-template-columns: 1fr;
    }
}
.footer {
    display: flex;
    padding: 16px;
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    color: rgb(130,130,130);
}

.redes a{
    text-decoration: none;
